.navbar{
    /* box-shadow: 0px 5px 10px 0px rgb(255, 255, 255); */
    /* position: fixed; */
    width: 32%;
    background:transparent;
    color: #000;
    opacity: 0.85;
    z-index: 100;
}

.navbar-container{
    display: flex;
    height: 64px;
    align-items: center;
    justify-content:flex-start;
}

/* .navbar-container .container
{
    display: flex;
    
} */

.menu-items{
    order: 2;
    display: flex;
}
.logo{
    order: 1;
    font-size: 2.3rem;
}

.menu-items li{
    list-style: none;
    margin-left: 1.5rem;
    font-size: 1.3rem;
}

.navbar a{
    color: #444;
    text-decoration: none;
    font-weight: 500;
    transition: color 0.3s ease-in-out;
}

.navbar a:hover{
    color: #117964;
}

.header-search-input{
    width: 100%;
    height: 10%;
    border-radius: 0px;

    background: rgba(255,255,255,0.4);
    border: none;
    position: relative;
    display: block;
    outline: none;
    /* width: 400px;
    height: 30p;
    top: 250px; */
    margin: 0 auto;
    padding: 10px;
    color: #333;
    -webkit-box-shadow: 0 2px 10px 1px rgba(0,0,0,0.5);
    box-shadow: 0 2px 10px 1px rgba(0,0,0,0.5); 
}
